<template>
  <div id="category">
    <!--  搜索区 -->
    <van-search placeholder="请输入搜索关键词" input-align="left"></van-search>
    <van-tree-select
      @click-nav="handleRight"
      height
      :items="items"
      :main-active-index.sync="active"
    >
      <!-- 右侧内容 -->
      <template #content>
        <RightContent :currentCategory="currentCategory" :subTitle="subTitle" />
      </template>
    </van-tree-select>
  </div>
</template>
<script>
import {getCategoryList,getCategoryItem} from '@/network/apis'
import RightContent from "./childComps/RightContent"
export default{
  name:"Category",
  data(){
    return {
      active: 0,
      // 左侧列表
      categoryList:[],
      //当前的类别详情
      currentCategory:{}
    }
  },
  components:{
    RightContent
  },
  methods:{
    getCategoryList(){
      getCategoryList().then(res=>{
      // console.log('Category==>',res);
      if(res.errno === 0){
          let {categoryList,currentCategory } = res.data;
          this.categoryList = categoryList;
          this.currentCategory = currentCategory;
      }
    })
    },
    getCategoryItem(params){
      getCategoryItem(params).then(res=>{
        // console.log('getCategoryItem==>',res);
        if(res.errno === 0){
          let {currentCategory} = res?.data;
          this.currentCategory = currentCategory;
        }
      })
    },
    handleRight(index){
      // console.log('handleRight==>',index);
      this.getCategoryItem({
        id:this.categoryList[this.active].id
      });
    }
  },
  computed:{
    items(){
      let items = [];
      if(this.categoryList){
        this.categoryList.forEach(item=>{
          items.push({text:item.name});
        });
      }
      return items;
    },
    isItemActive(){
      return function(index){
        return this.active === index;
      }
    },
    subTitle(){
      if(!this.items.length){
        return"";
      }
      return this.items[this.active].text;
    }

  },
  created(){
    this.getCategoryList();
  }
}
</script>
<style lang="less">
#category {
  display: flex;
  height: calc(100vh - 0.5rem);
  flex-direction: column;
  font-size: 0.14rem;
  .van-tree-select {
    flex: 1;
  }
  .van-tree-select__content {
    -webkit-box-flex: 4;
    -webkit-flex: 4;
    flex: 4;
  }
}
</style>