<template>
  <div class="right-content">
    <!-- banner区域 -->
    <div class="banner">
      <img :src="bannerUrl" alt />
      <div class="desc">{{frontDesc}}</div>
    </div>
    <div class="title">
      <span></span>
      <h2 class="text">{{subTitle}}</h2>
    </div>
    <!-- 内容区 -->
    <van-grid :column-num="3">
      <van-grid-item
        v-for="item in currentCategory.subCategoryList"
        :key="item.id"
        :icon="item.wap_banner_url"
        :text="item.name"
      />
    </van-grid>
  </div>
</template>
<script>
export default{
  name:"RightContent",
  props:{
    currentCategory:{
      type:Object,
      default(){
        return {};
      }
    },
    subTitle:{
      type:String,
      default:""
    }
  },
  computed:{
      bannerUrl(){
      if(!this.currentCategory){
        return "";
      }
      return this.currentCategory.banner_url
    },
    frontDesc(){
      if(!this.currentCategory){
        return "";
      }
      return this.currentCategory.front_desc;
    }
  }
}
</script>
<style scoped lang="less">
.right-content {
  .banner {
    width: 100%;
    box-sizing: border-box;
    position: relative;
    img {
      width: 96%;
      margin-left: 6px;
      height: 140px;
      border-radius: 8px;
      box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.6);
      filter: brightness(0.8);
    }
    .desc {
      position: absolute;
      top: 45%;
      width: 100%;
      text-align: center;
      color: #fff;
    }
  }
  .title {
    position: relative;
    height: 40px;
    span {
      height: 2px;
      width: 50%;
      background-color: #cccccc;
      position: absolute;
      margin: auto;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
    .text {
      width: 30%;
      background-color: #fff;
      position: absolute;
      margin: auto;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      line-height: 40px;
      text-align: center;
      font-size: 18px;
      font-weight: 500;
      margin: 0 auto;
      z-index: 9;
    }
  }
}
</style>